import React, { useState, useContext } from 'react';
import { View, TouchableHighlight, ScrollView } from 'react-native';
import useStyles from './styles/mainAuthScreen.module';
import HeaderButton from '../components/buttons/headerButton';
import BackSvg from '../assets/icons/backSvg';
import SettingsBody from '../components/settingsBody/settingsBody';
import DataInput from '../components/inputs/textInput/textInput';
import AddSvg from '../assets/icons/addSvg';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { useFocusEffect } from '@react-navigation/native';
import AuthContext from '../context/AuthContext';

export default function SettingsScreen({ navigation, route }) {
  const { channelId } = route.params;
  const { user } = useContext(AuthContext);
  const styles = useStyles();
  const [isAdmin, setIsAdmin] = useState(false);
  const [role, setRole] = useState('');
  
  const [isEditingNickname, setIsEditingNickname] = useState(false);
  const [channelData, setChannelData] = useState([]);
  const username = 'admin';
  const password = 'root';

  useFocusEffect(
    React.useCallback(() => {
      fetchChannelData();
    }, [])
  );
  const [inputText, setInputText] = useState({
    nickname: channelData.name || 'a',
  });
  const fetchChannelData = async () => {
    try {
      const response = await fetch(`https://linking-api.onrender.com/api/channels/${channelId}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Basic ${btoa(`${username}:${password}`)}`,
        },
      });
  
      if (response.ok) {
        const channelData = await response.json();
        
        setChannelData(channelData);
        setInputText((prevInputText) => ({
          ...prevInputText,
          nickname: channelData.channel.name || 'a',
        }));
        const isCreator = user?.id === channelData.creator?.id;
        setIsAdmin(isCreator);
      } else {
        alert('     ,     ');
      }
    } catch (error) {
      alert('    ', error);
    }
  };

  const handleDeleteChannel = async () => {
    try {
      const response = await fetch(`https://linking-api.onrender.com/api/channels/delete/${channelId}`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Basic ${btoa(`${username}:${password}`)}`,
        },
      });
  
      if (response.ok) {
        alert(' ');
        navigation.navigate('MainAuth'); 
      } else {
        alert('   ');
      }
    } catch (error) {
      alert('    ', error);
    }
  };
  
  const handleRoleChange = async (newRole) => {
    setRole(newRole);
    try {
      await AsyncStorage.setItem('role', newRole);
    } catch (error) {
      console.log('  :', error);
    }
  };

  const handleAddButtonClick = async () => {
    if (isEditingNickname) {
      setIsEditingNickname(false);
      try {
        const response = await fetch(`https://linking-api.onrender.com/api/channels/${channelId}/update?name=${encodeURIComponent(inputText.nickname)}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Basic ${btoa(`${username}:${password}`)}`,
          },
        });
  
        if (response.ok) {
          const channelResponse = await response.json();
          const updatedChannels = user.channels.map((channel) => {
          if (channel.id === channelResponse.id) {
            return { ...channel, name: channelResponse.name };
          }
          return channel;
        });

        const updatedUser = {
          ...user,
          channels: updatedChannels,
        };
        localStorage.setItem('user', JSON.stringify(updatedUser));

        } else {
          alert('    ');
        }
      } catch (error) {
        alert('    ', error);
      }
    } else {
      setIsEditingNickname(true);
    }
  };

  return (
    <View style={styles.containerSettings}>
      <View style={styles.channNameContainer}>
        <View style={styles.dataInputContainer}>
          <DataInput
            value={inputText.nickname}
            setValue={(text) => setInputText({ ...inputText, nickname: text })}
            placeholder={""}
            type={"nickname"}
            flex={false}
            editable={isEditingNickname}
          />
        </View>
        <TouchableHighlight onPress={handleAddButtonClick}>
          <AddSvg />
        </TouchableHighlight>
      </View>
      <View style={styles.settingsContainer}>
        <ScrollView style={{ flex: 1, scrollbarWidth: 0, flexDirection: 'column' }}>
        {channelData.members?.map((channel) => (
            <SettingsBody
             
              data={{
                key: channel.id,
                name: channel.user.name,
                role: channel.role.name,
                onRoleChange: handleRoleChange,
                creator: channel.role.creator,
                channelId: channelData
              }}
              
            />
          ))}
        </ScrollView>
      </View>

      <View style={styles.topLeft}>
        <TouchableHighlight onPress={() => navigation.navigate('Channel', { channelId: channelId })}>
          <BackSvg />
        </TouchableHighlight>
      </View>
      <View style={styles.bottomLeft}>
        <HeaderButton title={' '} onPress={handleDeleteChannel} disabled={!isAdmin} />
      </View>
    </View>
  );
}
